var tha,js;
export default {
	onLoad(){
		tha = this;
	},
	onUnload(){
		clearInterval(js)
		this.second = 0;
	},
	data() {
		return {
			token: '',
			phoneNum:'',
			password:'',
			username:'',
			tfpsw:true,
			code:'',
			invitation:'',
			xieyi:true,
			showPassword:false,
			second:0
		};
	},
	computed:{
		// yanzhengma(){
		// 	if(this.second==0){
		// 		return '获取验证码';
		// 	}else{
		// 		if(this.second<10){
