			//     });
			//     return;
			// }
			
			// console.log(this.phoneNum+","+this.token)
			
			uni.request({
			 	
				url: this.websiteUrl+'/user/signup',
			    data: {
					mobile:this.phoneNum,
					password:this.password,
					username:this.username,
					cityId:'5d132c7597464815c01eafc1'
