					//token: this.token
				},
				method: 'POST',
				dataType:'json',
			    success: (res) => {
					console.log(res,'-========-=-=-=-=')
					if(res.statusCode==200){
						
						uni.reLaunch({
							url: "../login/login" //跳转到首页
						});
						uni.showToast({title:"注册成功！",icon:'none'});
